html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-text);
  background-color: var(--background-color);
  color: var(--text-dark);
}

input,
button {
  font-family: var(--font-heading);
}

img {
  display: block;
  width: 100%;
  height: auto;
}

.header {
  background-color: var(--accurate);
  height: 6.25rem;
}

.brandlogo {
  position: relative;
  z-index: 999;
  width: 10.9375rem;
  height: 9.375rem;
  box-shadow: 0px 6px 6px var(--black);
}

.vintage {
  height: 100%;
  overflow: hidden;
  width: 50%;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}
.visually-hidden:focus {
  left: 0;
  width: auto;
  height: auto;
  background-color: #fff;
  padding: 0.5rem;
  border: 1px solid;
}

h1,
h2,
h3 {
  font-weight: 700;
  margin-top: 0;
}

h1 {
  font-family: var(--font-heading);
  font-size: 3.75rem;
  font-size: clamp(1.875rem, 1.02rem + 3.632vw, 3.75rem);
  line-height: 1;
  margin-block-end: 1.25rem;
  color: var(--primaryGreenDark);
}

p {
  font-size: 1rem;
  line-height: 1.3;
  margin-top: 0;
  margin-bottom: 1.25rem;
}

a:visited,
a:active {
  text-decoration: none;
}

a {
  text-decoration: underline;
}

:root {
  --accurate: hsl(85, 54%, 51%);
  --primaryGreenMed: hsl(85, 52%, 41%);
  --primaryGreenDark: hsl(85, 52%, 31%);
  --primaryGrey: hsl(220, 2%, 35%);
  --primaryLtGrey: hsl(210, 3%, 75%);
  --ltGrey: hsl(0, 0%, 62%);
  --medGrey: hsl(0, 0%, 44%);
  --dkGrey: hsl(0, 0%, 25%);
  --black:hsl(0, 0%, 6%);
  --white: hsl(240, 100%, 99%);
  --white60: hsla(240, 100%, 99%, .85);
}

:root {
  --font-text: "Lato", Helvetica, Arial, sans-serif; /* body font */
  --font-heading: "Roboto Slab", Helvetica, Arial, sans-serif; /* heading font */
}

.footer {
  background-color: var(--accurate);
  margin-inline: auto;
  text-align: center;
}
.footer h2 {
  font-family: var(--font-heading);
  font-size: 2rem;
  font-size: clamp(2rem, 1.54rem + 1.93vw, 3rem);
}
.wrapper {
  width: min(100% - 3rem, 75rem);
  margin-inline: auto;
  padding-block: 60px;
}
.wrapper_brand {
  padding-top: 0;
}
.wrapper__text {
  max-width: 43.75rem;
  width: 37.5rem;
  margin-inline: auto;
}
@media (max-width: 43.74875em) {
  .wrapper__text {
    width: 100%;
  }
}
.wrapper__text h2 {
  font-family: var(--font-heading);
  font-size: 1.75rem;
  font-size: clamp(1.75rem, 1.63rem + 0.48vw, 2rem);
  margin-bottom: 1.25rem;
  line-height: 2.2rem;
}
.wrapper__text h3 {
  font-family: var(--font-heading);
  font-size: 1.5rem;
  margin-bottom: 0;
}
.wrapper__fullWidth--section {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  overflow: hidden;
  position: relative;
  justify-content: flex-start;
  align-items: center;
}
.wrapper__fullWidth--row {
  display: flex;
  flex-direction: row;
  width: 100%;
}
.wrapper__fullWidth--image {
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  flex: 1;
  height: 18.75rem;
  width: 50%;
}
.wrapper__fullWidth--bkgImage {
  background-image: url("../../img/car-body-photo.jpg");
}
.wrapper__fullWidth--vintage {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 25rem;
  overflow: hidden;
}
.wrapper__fullWidth--text {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding-left: 1.25rem;
}
.wrapper__fullWidth--guarantee {
  width: 50%;
  padding: 1.25rem;
}
.wrapper__fullWidth--guarantee h2 {
  font-family: var(--font-heading);
  font-size: 3rem;
  letter-spacing: -0.5px;
  line-height: 1.1;
  text-align: left;
  font-weight: 700;
  color: var(--primaryGreenDark);
}
.wrapper__fullWidth--guarantee h3 {
  font-family: var(--font-heading);
  font-size: 2rem;
  letter-spacing: -0.5px;
  line-height: 1.1;
  font-weight: 700;
  color: var(--primaryGreenDark);
}

.sm-wrapper {
  margin-inline: auto;
  width: min(100% - 3rem, 75rem);
}
.sm-wrapper h2 {
  font-family: var(--font-heading);
  font-size: 2.25rem;
  letter-spacing: -0.5px;
  line-height: 1.1;
  text-align: center;
  font-weight: 700;
  color: var(--primaryGreenDark);
}

.fb-wrapper {
  display: grid;
  grid-template-columns: [fb-start] 1fr [content-start] min(100% - 3rem, 75rem) [content-end] 1fr [fb-end];
}
.fb-wrapper > * {
  grid-row: 1/2;
}
.fb-wrapper__full {
  grid-column: fb-start/fb-end;
  align-self: end;
}
.fb-wrapper__content {
  grid-column: content-start/content-end;
}

.feature-image {
  background: url(/img/hero_accident_1920x750.jpg) no-repeat;
  background-position-y: 0%;
  background-size: auto;
  background-position: left top;
  background-size: cover;
  left: 50%;
  padding: 0;
  position: relative;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  margin-top: 0;
  margin-bottom: 0;
  height: 34.375rem;
  overflow: hidden;
  clear: both;
}
@media (max-width: 56.24875em) {
  .feature-image {
    background-position: left top;
  }
}
@media (max-width: 43.74875em) {
  .feature-image {
    height: 31.25rem;
    padding-top: 2.5rem;
  }
}

.hero {
  background-position: left top;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
}
@media (max-width: 43.74875em) {
  .hero {
    background-position: left top;
    justify-content: flex-start;
    padding-top: 6.25rem;
  }
}
.hero__logo {
  margin-bottom: 3.75rem;
}
.hero__callout {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  border-radius: 0.625rem;
  padding: 1.25rem;
  width: 30%;
  background-color: var(--white60);
}
@media (max-width: 56.24875em) {
  .hero__callout {
    width: 60%;
  }
}
@media (max-width: 43.74875em) {
  .hero__callout {
    width: 90%;
  }
}
.hero__callout h2 {
  font-family: var(--font-heading);
  font-size: 1.5rem;
  font-size: clamp(1.5rem, 1.04rem + 1.93vw, 2.5rem);
  color: var(--primaryGreenDark);
  margin-bottom: 0;
}
.hero__headline {
  display: flex;
  align-items: center;
  flex-direction: column;
  position: absolute;
  top: 65%;
  z-index: 900;
  width: 100%;
}

.mainText {
  background-color: var(--accurate);
}

.footer {
  padding: 1.25rem;
}